body,html {
  margin: 0;
  font-family: 'Manrope', sans-serif;
  width: 100%;
  overflow-x: hidden;
  height: 100vh;
  overflow-y: auto;
}
.content-wrapper {
  padding: 40px 160px;
}
.header {
  background: url(./static/stadium.png);
  background-size: contain;
  height: 100vh;
  width: 100vw;
  overflow: hidden;
  background-position: center;
  background-attachment: fixed;
  background-repeat: no-repeat;
  position: relative;
}
.header img.hand {
  position: absolute;
  bottom: 0;
  left: calc((100vw - 85vh) / 2);
}

.header .title {
  position: absolute;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
}

.header .title > * {
  background: #ffffff80;
  padding: 4px 8px;
  /* backdrop-filter: blur(200px); */
  background: linear-gradient(to right, #eff1f3 4%, #e2e2e2 25%, #eff1f3 36%);
}

.header .title h1 {
  color: #ff4a3a;
  margin: 0;
  font-size: 32px;
  font-weight: bolder;
}
.header .title span {
  font-size: 20px;
  font-weight: normal;
  color: #1e1e6390;
  display: block;
}
.header .title h2 {
  color: #1e1e63;
  font-weight: 900;
  margin: 0;
}

.header .title img {
  width: 64px;
}

@media(min-width: 700px) {
  .header img.hand{
    height: 100%;
  }
  .header .title {
    bottom: 50px;
    left: 50px;
  }
}
@media(max-width: 700px) {
  .header {
    background-size: contain;
  }
  .header img.hand {
    width: 120vw;
    left: -5vw
  }
  .header .title {
    top: 24px;
    left: 24px;
    right: 24px;
  }
  .header .title h1 {
    font-size: 24px;
  }
  .header .title span {
    font-size: 20px;
  }
}


.game {
  padding: 120px 0;
  background: linear-gradient(45deg, #010070, #00003f);
  background-repeat: no-repeat;
  background-attachment: fixed;
  background-position: left center;
  /* background-size: 50% auto; */
}

.game.feature {
  background: #fff;
}

.game h2, .game h3 {
  color: #fff;
  text-align: center;
  padding: 0 32px;
  margin: 0;
}

.game h2 {
  margin-bottom: 32px;
}

.game.feature h2, .game.feature h3 {
  color: #000;
}
.game h3 {
  font-weight: 100;
}

.game .content-wrapper {
  display: flex;
  align-items: center;
  justify-content: center;
  max-width: 1200px;
  margin: auto;
}

.game .instruction_wrapper {
  display: flex;
  align-items: center;
  justify-content: space-around;
  margin-bottom: -40px;
}

.game .instruction_wrapper:nth-child(2n) {
  flex-direction: row-reverse;
}

.game .instruction {
  padding: 24px;
  margin: 20px 0;
  width: 50%;
  font-size: 30px;
  font-weight: lighter;
}

.game .instruction {
  color: rgba(255,255,255,0.5);
  cursor: pointer;
}

.game .instruction b {
  font-size: 34px;
  color: #fff;
}

.game.feature .instruction b {
  color: #010070;
}

.game.feature .instruction {
  color: rgba(0,0,0,0.5);
}

.game img {
  max-height: 60vh;
  border-radius: 40px;
  border: 12px solid #000;
}

.game.feature img {
  width: 50%;
  border: none;
}

@media(max-width: 1024px) {
  .content-wrapper {
    padding: 60px;
  }
  .game .instruction b {
    font-size: 28px;
  }
  .game .instruction {
    font-size: 24px;
  }
  .game.feature img {
    width: 100%;
    border: none;
  }
}

@media(max-width: 720px) {
  .game .instruction_wrapper,.game .instruction_wrapper:nth-child(2n) {
    flex-direction: column;
    text-align: center;
    margin-bottom: 0;
  }
  .game .instruction_wrapper .instruction {
    width: 100%;
  }
}

.footer {
  background: #05042f;
  text-align: left;
  color: #ffffff70;
  padding: 32px 20px;
}

.footer b {
  color: #fff;
}

.footer .links {
  margin-bottom: 8px;
}

.footer .links a {
  text-decoration: none;
  color: rgba(255, 255, 255, 0.7);
  display: block;
  text-align: left;
  transition: .5s ease;
  display: inline-block;
  margin-right: 16px;
}

.footer .links a:hover {
  color: rgba(255, 255, 255, 1);
}
